/*------------------------------
	TABS
------------------------------*/

.tabs .nav-tabs {
    border-width: 2px;
}

.tabs .nav-tabs > li {
    margin-bottom: 0;
    background-color: #FFFFFF;
    margin-right: 5px;
    padding: 0;
