}

.tabs .nav-tabs > li.active > a,
.tabs .nav-tabs > li.active > a:focus,
.tabs .nav-tabs > li.active > a:hover {
    color: #FFFFFF;
    border: 0;
}

.tabs .tab-content {
    padding-top: 20px;
}

.tabs .tab-content h4,
.tabs .tab-content h5 {
    font-weight: bold;
    color: #777777;
