}

/*------------------------------
	BREADCRUMB
------------------------------*/

.breadcrumb-wrapper {
    background-color: #FFFFFF;
    padding: 10px 0;
}

.breadcrumb-wrapper h2 {
    margin: 5px 0 0;
    font-size: 20px;
    font-weight: bold;
    color: #777777;
    letter-spacing: -1px;
}

.breadcrumb-wrapper p {
