}

/*------------------------------
	PAGINATION
------------------------------*/

.pagination-wrapper {
    padding: 15px 10px 5px;
    text-align: center;
}

.pagination {
    margin: 0;
    border-radius: 0;
}

.pagination > li > a,
.pagination > li > span {
    margin: 0 5px 5px;
    border: 2px solid #D0D0D0;
    color: #CCCCCC;
    font-weight: bold;
    font-size: 16px;
    width: 38px;
    height: 38px;
    text-align: center;
    padding: 6px 0 0;
}

.pagination > li:first-child > a,
.pagination > li:first-child > span,
.pagination > li:last-child > a,
.pagination > li:last-child > span {
    border-radius: 0;
}

.pagination > li > a:hover,
.pagination > li > a:focus {
    background-color: #FFFFFF;
    color: #AAAAAA;
    border-color: #B0B0B0;
}

.pagination > li.active > a,
.pagination > li.active > span,
.pagination > li.active > a:hover,
.pagination > li.active > a:focus {
    background-color: #FFFFFF;
}

.pagination-lg > li > a,
.pagination-lg > li > span {
    font-size: 22px;
    width: 46px;
    height: 46px;
    padding: 7px 0 0;
}

.pagination-sm > li > a,
.pagination-sm > li > span {
    font-size: 12px;
    width: 30px;
    height: 30px;
    padding: 5px 0 0;
}

