/*------------------------------
	OWL CAROUSEL
------------------------------*/

.owl-theme .owl-controls {
    margin: 0;
}

/* OWL - NAV */

.owl-theme .owl-controls .owl-nav div {
    margin: 0;
    width: 40px;
    height: 40px;
    padding: 4px 0 0;
    text-align: center;
    border-radius: 50%;
    font-size: 23px;
    background-color: #666666;
    color: #FFFFFF;
    position: absolute;
    top: 50%;
    margin-top: -20px;
}

.owl-theme .owl-controls .owl-nav div.owl-prev {
    left: -37px;
}

.owl-theme .owl-controls .owl-nav div.owl-next {
    right: -37px;
}

.owl-theme.nav-inside .owl-controls .owl-nav div {
    opacity: 0.6;
    transition: all 0.2s ease-in;
}

.owl-theme.nav-inside .owl-controls .owl-nav div:hover {
    opacity: 1;
}

.owl-theme.nav-inside .owl-controls .owl-nav div.owl-prev {
    left: 20px;
}

.owl-theme.nav-inside .owl-controls .owl-nav div.owl-next {
    right: 20px;
}

body.page-boxed .owl-theme.nav-inside .owl-controls .owl-nav div.owl-prev {
    left: -80px;
}

body.page-boxed .owl-theme.nav-inside .owl-controls .owl-nav div.owl-next {
    right: -80px;
}

/* OWL - PAGINATION */

.owl-theme .owl-controls .owl-dots {
    text-align: center;
}

.owl-theme .owl-controls .owl-dot {
    width: 16px;
    height: 16px;
    margin: 10px 5px 5px;
    background-color: #F0F0F0;
    border: 2px solid rgba(0, 0, 0, 0.1);
    border-radius: 50%;
    display: inline-block;
}

.owl-theme.nav-inside .owl-controls .owl-dots {
    position: absolute;
    right: 20px;
    bottom: 20px;
    border-radius: 20px;
    background-color: rgba(0, 0, 0, 0.2);
    padding: 0 5px;
}

.owl-theme.nav-inside .owl-controls .owl-dot {
    border: 0;
}

/*------------------------------
	SCROLL TOP
------------------------------*/

#scrolltop {
    position: fixed;
    right: 15px;
    bottom: 15px;
    width: 40px;
    height: 40px;
    text-align: center;
    font-size: 23px;
    padding-top: 3px;
    background-color: #555555;
    color: #FFFFFF;
    transition: all 0.3s ease-in;
    z-index: 1040;
    cursor: pointer;
    transform: translateY(70px);
}

#scrolltop.in {
    transform: translateY(0);
}

/*------------------------------
	HEADER
------------------------------*/

.top-header {
    box-shadow: 0 20px 20px -20px rgba(0, 0, 0, 0.2) inset;
    background-color: #F9F9F9;
    height: 40px;
    border-bottom: 1px solid #EBEBEB;
}

.top-header .contacts {
    margin: 0;
}

.top-header .contacts li,
.vertical-menu-bottom .contacts li {
    color: #888888;
    font-weight: 600;
    margin-top: 8px;
}

.top-header .contacts li i,
.vertical-menu-bottom .contacts li i {
    border: 2px solid #E87169;
    border-radius: 50%;
    padding: 5px 0 0;
    text-align: center;
    width: 26px;
    height: 26px;
    margin-right: 5px;
    font-size: 13px;
}

.top-header .links,
.top-header .languages {
    display: inline-block;
    padding: 10px 0 0 10px;
}

.top-header .links li {
    padding: 0 8px;
}

.top-header .links a {
    color: #888888;
    position: relative;
}

.top-header .links a:after {
    width: 4px;
    height: 4px;
    position: absolute;
    border-radius: 50%;
    right: -11px;
    top: 50%;
    margin-top: -1px;
    content: "";
    background-color: #888888;
}

.top-header .links li:last-child a:after {
    display: none;
}

.top-header-dark {
    background-color: #393939;
    border-color: #2B2B2B;
}

.top-header-dark .contacts li,
.navbar-dark .vertical-menu-bottom .contacts li {
    color: #AAAAAA;
}

.top-header-dark .links a {
    color: #AAAAAA;
}

/* HEADER - NAVBAR */

header.navbar {
    margin: 0;
}

header.navbar-default {
    background-color: #FFFFFF;
    height: 100px;
    border-color: #EBEBEB;
    transition: height 0.3s ease-in;
}

header.navbar > .container {
    position: relative;
}

header.navbar .navbar-brand {
    padding: 37px 15px;
    height: auto;
    font-size: 50px;
    font-weight: 600;
    transition: all 0.3s ease-in;
}

header.navbar .navbar-brand span {
    color: #666666;
    transition: color 0.3s ease-in;
}

header.navbar .navbar-brand:hover {
    color: #666666;
}

header.navbar .navbar-text {
    color: #999999;
    font-weight: 300;
    font-size: 16px;
